.announcement-bar {
	.wrapper {
		padding: 10px 0;
		align-items: center;
	}
	.annoubar-column {
		text-transform: uppercase;
		font-size: 11px;
		display: flex;
		align-items: center;
		.menu-list {
			display: flex;
			flex-wrap: wrap;
			li {
				padding: 3px 10px;
				&:first-child {
					padding-inline-start: 0;
				}
			}
		}
		p {
			margin: 0;
		}
		svg {
			width: 14px;
			height: 14px;
		}
		.account {
			display: flex;
			align-items: center;
			>a {
				padding: 3px 12px;
				display: flex;
			}
			.account-action {
				a[href="/account/register"] {
					margin-inline-start: 6px;
					padding-inline-start: 8px;
					border-left: 1px solid currentColor;
				}
			}
			>a {
				svg {
					@include margin-end(5px)
				}
			}
		}
		.annoubar-item {
			display: flex;
		}
		.currency-with-language {
			padding: 0 12px;
			height: 14px;
			margin-top: -1px;
			a {
				height: 14px;
				display: block;
			}
		}
	}
}