.menu-list {
	.menu-item {
		display: flex;
		align-items: center;
		justify-content: space-between;
		flex-wrap: wrap;
		font-size: var(--body-font-size);
		svg {
			width: 9px;
			height: 9px;
		}
	}
}
.main-menu {
	.menu-item {
		.dropdown-menu {
			@extend %transition;
			transform: translateY(5px);
			&:not(.lazyloaded) {
				min-height: 50px;
				display: flex;
			}
			ul {
				padding: 5px 15px;
				width: 100%;
				>li {
					height: auto;
					line-height: 1.5;
					padding: 9px 0;
					font-size: var(--body-14);
					position: relative;
					&:not(:last-child) {
						border-bottom: 1px solid var(--body-border-color);
					}
					a {
						transition: unset;
					}
				}
			}
		}
		&:hover, &.active {
			@media (min-width: 992px) {
				.dropdown-menu {						
					opacity: 1;
					visibility: visible;
					transform: translateY(0);
				}
			}
		}
	}
	.mega-menu {
		.mega-menu-list {
			padding: 20px;
		}
		.title {
			>a {
				text-transform: uppercase;
				display: block;
				padding-bottom: 10px;
				margin-bottom: 10px;
				border-bottom: 1px solid var(--body-border-color);
			}
		}
		.dropdown-menu {
			ul {
				padding: 0;
			}
			.mega-menu-item {
				padding: 0 15px;
				margin-bottom: 15px;
				.mul-product_wrapper {
					li {
						border: 0;
						padding: 5px 0;
						&:not(:first-child) {
							margin-bottom: 10px;
						}
					}
					.label {
						position: relative;
						margin-bottom: 5px;
						right: auto;
						display: block;
					}
				}
			}
			.menu-list .menu-item {
				border: 0;
				padding: 5px 0;
			}
		}
		.menu-list .menu-item {
			display: block;
		}
		.menu-list_wrapper {
			>li:first-child {
				margin-bottom: 15px;
			}
		}
		.banner_wrapper {
			>div {
				padding-bottom: 15px;
			}
			.text-center>div {
				padding: 30px 0;
			}
			.text-container {
				padding: 15px 20px;
				p {
					margin: 0;
					color: var(--color-second);
					line-height: 1.4;
				}
				.text-1 {
					margin-bottom: 15px;
					color: var(--main-color);
				}
			}
			.section-under {
				display: block;
				margin-top: 20px;
				img {
					margin-bottom: 10px;
				}
			}
		}
		.product-card {
			.product-card_row {
				>* {
					line-height: initial;
				}
			}
			.card-button {
				svg {
					width: 20px;
					height: 20px;
				}
			}
			.product-card_action {
				display: none;
			}
		}
	}
	.mul-product {
		.image {
			width: 30%;
			max-width: 85px;
			svg {
				width: 100%;
				height: 100%;
			}
		}
		.info {
			flex: 1;
			@include padding-start(15px);
		}
	}
}
.header-container {
	.dropdown-menu {
		min-width: 250px;
	}
	.horizontal-nav {
		>ul {
			li.dropdown-submenu {
				li.dropdown-submenu {					
					.dropdown-menu {
						@include ab(0,100%,n,n);
						opacity: 0;
						visibility: hidden;
						transform: translateY(5px);
					}
					&:hover {
						@media (min-width: 992px) {							
							.dropdown-menu {						
								opacity: 1;
								visibility: visible;
								transform: translateY(0);
							}
						}
					}
				}
			}
		}
		.menu-item:not(.dropdown-submenu) {
			.svg_element-ui {
				display: none;
			}
		}
	}
}

/*vertical*/
.vertical-nav {
	.menu-list {
		>li.dropdown-submenu {
			.dropdown-menu {				
				@include ab(0,100%,n,0);
				opacity: 0;
				visibility: hidden;
				transform: translateY(5px);
				@extend %transition;
				.dropdown-submenu .dropdown-menu {
					opacity: 0;
					visibility: hidden;
					transform: translateY(5px);
				}
				.dropdown-submenu:hover {	
					@media (min-width: 992px) {					
						.dropdown-menu { 
							opacity: 1;
							visibility: visible;
							transform: translateY(0);
							box-shadow: 2px 2px 4px 1px rgba(0,0,0,0.2);
						}		
					}								
				}
			}
			&:hover, &.active {
				@media (min-width: 992px) {					
					.dropdown-menu {
						opacity: 1;
						visibility: visible;
						transform: translateY(0);
						box-shadow: 2px 2px 4px 1px rgba(0,0,0,0.2);
					}
				}				
			}
		}
		.mega-menu {
			.dropdown-menu {
				width: 100%;
				min-width: 745px;
				max-height: 650px;
				overflow-y: auto;
				.mega-menu-list {
					padding: 20px 30px;
				}
				.mega-menu-item {
					padding: 0 15px;
					.menu-item {
						padding: 8px 0;
						border: 0;
						&.title {
							padding: 0;
							display: block;
							width: 100%;
							>a {
								font-weight: 500;
								text-transform: uppercase;
								border-bottom: 1px solid var(--body-border-color);
								padding: 12px 0;
								display: block;
							}
						}
					}
					.menu-list {
						padding: 5px 0;
					}
				}
			}
			.banner_wrapper {
				>div {
					padding-bottom: 15px;
				}
				.text-container {
					padding: 15px 20px;
					p {
						margin: 0;
						color: var(--color-second);
						line-height: 1.4;
					}
					.text-1 {
						margin-bottom: 15px;
						color: var(--main-color);
					}
				}
				.section-under {
					display: block;
					line-height: 1;
					img {
						margin: 15px 0;
					}
				}
			}
		}
		.menu-item {
			.svg_element-ui {
				display: none;
			}
			&.dropdown-submenu {
				.svg_element-ui {
					display: block;
				}
			}
		}
		li {
			font-size: var(--body-14);
		}
		.mul-product_wrapper {
			.title {
				padding-left: 0;
				padding-right: 0;
				border: 0;
				>a {
					text-transform: uppercase;
					display: block;
					font-weight: 500;
					padding-bottom: 10px;
					margin-bottom: 10px;
					border-bottom: 1px solid var(--body-border-color);
				}
			}
			.image {
				position: relative;
				width: 100px;
				margin-right: 10px;
			}
			.name {
				margin-bottom: 5px;
				display: block;
			}
		}
	}
	&.active {		
		>.dropdown-menu {
			transform: translateY(0);
			opacity: 1;
			visibility: visible;
			border: 2px solid var(--main-color);
			border-top: 0;
			.menu-list>li.dropdown-submenu .dropdown-menu {
				left: 101%;
			}
		}
	}
	@media (min-width: 1199px) {
		.menu-list .mega-menu .dropdown-menu {
			min-width: 900px;
		}
	}
}
/*end vertical*/
